package com.hclee.coffee_vote.vote_detail

import androidx.lifecycle.ViewModel
import androidx.lifecycle.viewModelScope
import com.hclee.coffee_vote.util.createUuid
import com.hclee.coffee_vote.util.getIdOrEmpty
import com.hclee.coffee_vote.util.setId
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.flow.update
import kotlinx.coroutines.launch

class VoteDetailViewModel : ViewModel() {

    private val _state = MutableStateFlow(VoteDetailState())
    val state = _state.asStateFlow()

    fun start(voteCode: String) {
        _state.update {
            it.copy(voteCode = voteCode)
        }

        viewModelScope.launch {
            val id = getIdOrEmpty().ifBlank {
                val newId = createUuid()

                setId(newId)

                newId
            }

            _state.update {
                it.copy(
                    id = id
                )
            }
        }
    }
}
