package com.hclee.coffee_vote.domain.usecase

import kotlinx.coroutines.CoroutineDispatcher
import kotlinx.coroutines.withContext
import kotlin.coroutines.EmptyCoroutineContext

abstract class CoroutineUseCase<in P, R> {

    private var coroutineDispatcher: CoroutineDispatcher? = null

    protected abstract suspend fun executeInternal(parameter: P): R

    fun setDispatcher(coroutineDispatcher: CoroutineDispatcher): CoroutineUseCase<P, R> {
        this.coroutineDispatcher = coroutineDispatcher
        return this
    }

    suspend fun execute(parameter: P): R {
        val coroutineContext = coroutineDispatcher ?: EmptyCoroutineContext
        return withContext(coroutineContext) {
            executeInternal(parameter)
        }
    }
}
