package com.hclee.coffee_vote.data.repository

import com.hclee.coffee_vote.data.datasource.CoffeeDataSource
import com.hclee.coffee_vote.domain.entity.IcedCoffee
import com.hclee.coffee_vote.domain.repository.CoffeeRepository

class CoffeeRepositoryImpl : CoffeeRepository {

    private val coffeeDataSource = CoffeeDataSource()

    override suspend fun getIcedCoffeeList(): List<IcedCoffee> {
        return coffeeDataSource.getIcedCoffeeList()
            .map {
                IcedCoffee(
                    id = it.id,
                    title = it.title,
                    image = it.image,
                    description = it.description,
                    ingredients = it.ingredients
                )
            }
    }
}
