package com.hclee.coffee_vote.domain.usecase

import com.hclee.coffee_vote.data.repository.CoffeeRepositoryImpl
import com.hclee.coffee_vote.domain.entity.IcedCoffee
import com.hclee.coffee_vote.domain.repository.CoffeeRepository

class GetIcedCoffeeList : CoroutineUseCase<Unit, GetIcedCoffeeList.Response>() {

    private val coffeeRepository: CoffeeRepository = CoffeeRepositoryImpl()

    override suspend fun executeInternal(parameter: Unit): Response {
        return runCatching {
            coffeeRepository.getIcedCoffeeList()
        }.map {
            Response.Success(it)
        }.getOrElse {
            Response.Failure(it)
        }
    }

    sealed class Response {

        class Success(
            val icedCoffeeList: List<IcedCoffee>,
        ) : Response()

        class Failure(
            val throwable: Throwable,
        ) : Response()
    }
}
