package com.hclee.coffee_vote.util

import io.ktor.client.utils.*
import kotlinx.browser.document

actual fun getCurrentUrl(): String {
    return document.URL
}

actual suspend fun getIdOrEmpty(): String {
    val cookie = document.cookie

    return cookie.split(";")
        .find { it.startsWith("id=") }
        ?.substringAfter("id=")
        .orEmpty()
}

actual suspend fun setId(id: String) {
    require(id.isNotBlank())

    val cookie = document.cookie
    val tenDaysInSeconds = 60 * 60 * 24 * 10

    val now = js("new Date()")
    val hours = js("now.getHours()") as Int
    val minutes = js("now.getMinutes()") as Int
    val seconds = js("now.getSeconds()") as Int

    val secondsUntilNextZeroAm = 60 - seconds
    val minutesUntilNextZeroAm = 59 - minutes
    val hoursUntilNextZeroAm = 23 - hours

    val maxAge = tenDaysInSeconds + secondsUntilNextZeroAm + (minutesUntilNextZeroAm * 60) + (hoursUntilNextZeroAm * 60 * 60)
    // maxAge:   10 +    0   

    document.cookie = if (cookie.isBlank()) {
        "id=$id; ${CacheControl.MAX_AGE}=${maxAge}"
    } else {
        "${cookie}; id=$id; ${CacheControl.MAX_AGE}=${maxAge}"
    }
}

actual suspend fun createUuid(): String {
    val uuid = UUID.v4()

    return uuid
}

@JsModule("uuid")
@JsNonModule
external object UUID {

    fun v4(): String
}
