package com.hclee.coffee_vote

import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.ui.Modifier
import androidx.navigation.NavType
import androidx.navigation.compose.NavHost
import androidx.navigation.compose.composable
import androidx.navigation.compose.rememberNavController
import androidx.navigation.navArgument
import com.hclee.coffee_vote.theme.AppTheme
import com.hclee.coffee_vote.vote_creation.VoteCreationScreen
import com.hclee.coffee_vote.vote_creation.VoteCreationViewModel
import com.hclee.coffee_vote.vote_detail.VoteDetailScreen
import com.hclee.coffee_vote.vote_detail.VoteDetailViewModel

@Composable
internal fun App() = AppTheme {
    Column(
        modifier = Modifier
            .fillMaxSize()
    ) {
        val navController = rememberNavController()

        NavHost(
            modifier = Modifier.fillMaxSize(),
            navController = navController,
            startDestination = Screen.VoteCreation.name
        ) {
            composable(route = Screen.VoteCreation.name) {
                val voteCreationViewModel by remember { mutableStateOf(VoteCreationViewModel) }

                VoteCreationScreen(
                    viewModel = voteCreationViewModel,
                    onNavToVoteDetail = { navItem ->
                        navController.navigate(
                            route = "${Screen.VoteDetail.name}?voteCode=${navItem.voteCode}",
                        )
                    }
                )
            }
            composable(
                route = "${Screen.VoteDetail.name}?voteCode={voteCode}",
                arguments = listOf(
                    navArgument("voteCode") {
                        type = NavType.StringType
                        defaultValue = ""
                    }
                )
            ) { backStackEntry ->
                val voteDetailViewModel by remember { mutableStateOf(VoteDetailViewModel()) }

                VoteDetailScreen(
                    viewModel = voteDetailViewModel,
                    voteCode = backStackEntry.arguments?.getString("voteCode").orEmpty(),
                    onNavToBack = {
                        navController.popBackStack()
                    }
                )
            }
        }
    }
}
