package com.hclee.coffee_vote.data.datasource

import com.hclee.coffee_vote.data.api.HttpClient
import com.hclee.coffee_vote.data.api.IcedCoffeeData
import io.ktor.client.call.*

class CoffeeDataSource {

    suspend fun getIcedCoffeeList(): List<IcedCoffeeData> {
        val response = HttpClient.get("https://api.sampleapis.com/coffee/iced")

        return response.body<List<IcedCoffeeData>>()
    }
}
