package com.hclee.coffee_vote.vote_creation

import androidx.lifecycle.ViewModel
import androidx.lifecycle.viewModelScope
import com.hclee.coffee_vote.Event
import com.hclee.coffee_vote.domain.usecase.GetIcedCoffeeList
import com.hclee.coffee_vote.util.getCurrentUrl
import com.hclee.coffee_vote.vote_detail.VoteDetailNavItem
import io.ktor.http.*
import kotlinx.collections.immutable.toImmutableList
import kotlinx.coroutines.async
import kotlinx.coroutines.coroutineScope
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.flow.update
import kotlinx.coroutines.launch

object VoteCreationViewModel : ViewModel() {

    private val _state = MutableStateFlow(VoteCreationState())
    val state = _state.asStateFlow()
    private var hasInitialized = false

    private val getIcedCoffeeList = GetIcedCoffeeList()

    fun start() {
        updateVoteCode("")

        if (hasInitialized) {
            return
        }

        hasInitialized = true

        val voteCode = runCatching {
            Url(getCurrentUrl()).parameters
        }.map {
            it["voteCode"]
        }.getOrNull().orEmpty()

        if (voteCode.isNotBlank()) {
            navToVoteDetailFor(voteCode)
        }
    }

    fun onVoteCodeInputChanged(input: String) {
        updateVoteCode(input)
    }

    private fun updateVoteCode(voteCode: String) {
        _state.update {
            it.copy(
                voteCodeInput = voteCode,
                isEnterVoteButtonEnabled = voteCode.isNotBlank()
            )
        }
    }

    fun onCreateVoteButtonClicked() {
        viewModelScope.launch {
            requestCoffeeMenuList()
        }
    }

    private suspend fun requestCoffeeMenuList() = coroutineScope {
        _state.update {
            it.copy(isProgressIndicatorVisible = true)
        }

        val icedCoffeeList = async {
            getIcedCoffeeList.execute(Unit)
        }.await()

        _state.update {
            val icedCoffeeUiItems = (icedCoffeeList as? GetIcedCoffeeList.Response.Success)
                ?.icedCoffeeList
                .orEmpty()
                .map {
                    IcedCoffeeUiItem(
                        title = it.title,
                        image = it.image,
                        description = it.description
                    )
                }.toImmutableList()

            it.copy(
                isProgressIndicatorVisible = false,
                icedCoffeeUiItems = icedCoffeeUiItems,
                icedCoffeeDescription = icedCoffeeUiItems.randomOrNull()?.description.orEmpty()
            )
        }
    }

    fun onEnterVoteButtonClicked() {
        navToVoteDetailFor(voteCode = _state.value.voteCodeInput)
    }

    private fun navToVoteDetailFor(voteCode: String) {
        if (voteCode.isNotBlank()) {
            _state.update {
                it.copy(
                    navToVoteDetail = Event(
                        VoteDetailNavItem(
                            voteCode = voteCode
                        )
                    )
                )
            }
        }
    }
}
